@import url("https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&family=Montserrat:wght@500;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: grid;
  place-items: center;
  min-height: 100vh;
  font-family: "Montserrat", san-serif;
  font-size: 0.875rem;
  background-color: hsl(30, 38%, 92%);
}

p,
del {
  color: hsl(228, 12%, 48%);
}

.container {
  width: 21rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0.5rem;
  background-color: hsl(0, 0%, 100%);
}
@media (min-width: 90rem) {
  .container {
    min-width: 41.25rem;
    max-width: 100%;
    min-height: 23.75rem;
    flex-direction: row;
  }
}
.container .product__image {
  width: 100%;
  height: 16.25rem;
  background-image: url(./../images/image-product-mobile.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 0.5rem 0.5rem 0rem 0rem;
}
@media (min-width: 90rem) {
  .container .product__image {
    flex: 1;
    background-image: url(./../images/image-product-desktop.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 27.5rem;
    border-radius: 0.5rem 0rem 0rem 0.5rem;
  }
}
.container .product__content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
}
@media (min-width: 90rem) {
  .container .product__content {
    padding: 2rem;
  }
  .container .product__content p {
    font-size: 1.1619rem;
    line-height: 1.5;
  }
  .container .product__content h1 {
    font-size: 2.5rem;
  }
}
.container .product__content .perfume {
  text-transform: uppercase;
  letter-spacing: 0.3rem;
}
.container .product__content h1 {
  font-family: "Fraunces", san-serif;
  font-size: 1.875rem;
  font-weight: 700;
}
.container .product__content p {
  line-height: 1.5;
}
.container .product__content .price {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.container .product__content .price p.current {
  font-family: "Fraunces", san-serif;
  font-size: 1.875rem;
  font-weight: 700;
  color: hsl(158, 36%, 37%);
}
.container .product__content button {
  border: none;
  border-radius: 0.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 3rem;
  background-color: hsl(158, 36%, 37%);
  color: hsl(0, 0%, 100%);
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.15s ease-in-out;
}
.container .product__content button:hover, .container .product__content button:focus {
  background-color: hsl(157, 36%, 23%);
}